<template>
  <div id="app">
    <div id="content">
      <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
      <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
    </div>
    <div id="foot">
      <ThankEditor ref="thankEditor" :markdown="currentThankMarkdown" :enableHtml="enableHtml"></ThankEditor>
    </div>
  </div>
</template>

<script>
  import StyleEditor from './components/StyleEditor'
  import ResumeEditor from './components/ResumeEditor'
  import ThankEditor from './components/ThankEditor'
  import './assets/reset.css'

  export default {
    name: 'app',
    components: {
      StyleEditor,
      ResumeEditor,
      ThankEditor
    },
    data() {
      return {
        interval: 5,
        currentStyle: '',
        enableHtml: false,
        fullStyle: [
          `/*
* Inspired by http://strml.net/
* 
* 
*/

/*  */
* {
  transition: all .1s;
}
/*  */
html {
  color: rgba(222,222,222,.8); background: rgba(128,138,135,.5);
}
#content{
  height:70vh;
  margin:0;
}
#foot{
  height:29vh;
  margin:0;
}
/*  */
.styleEditor {
  font-size: 1.2rem;
  padding: .5em;
  border: 1px solid rgb(255,0,0,);
  margin: .5em;
  overflow: auto;
  width: 38vw; 
  height: 75vh;
  background: rgb(20,20,20);
}
/*  */
.token.selector{ color: rgb(144,230,42); }
.token.property{ color: rgb(116,217,238); }
.token.punctuation{ color: rgb(255,255,255); }
.token.function{ color: rgb(40,160,150); }

/* 3D */
html{
  perspective: 1000px;
  -webkit-perspective: 1000px;
}
.styleEditor {
  position: fixed; 
  left: 0; 
  top: 0;
  -webkit-transition: none;
  transition: none;
  -webkit-transform: rotateY(10deg) translateZ(-100px) ;
  -ms-transform: rotateY(10deg) translateZ(-100px) ;
  -o-transform: rotateY(10deg) translateZ(-100px) ;
  transform: rotateY(10deg) translateZ(-100px) ;
}

/*  */
.resumeEditor{
  position: fixed; 
  right: 0; 
  top: 0;
  margin: .5em;
  width: 60vw; 
  height: 75vh;
  border: 1px solid rgb(0,140,255);
  background: rgb(255,255,255); 
  color: #222;
  overflow: auto;
  -webkit-transition: none;
  transition: none;
  -webkit-transform: rotateY(-5deg) translateZ(-100px) ;
  -ms-transform: rotateY(-5deg) translateZ(-100px) ;
  -o-transform: rotateY(-5deg) translateZ(-100px) ;
  transform: rotateY(-5deg) translateZ(-100px) ;
}
/*  */
`, `
/*MarkdownHTML
 *HTML
*/
.resumeEditor {
  padding: 2vw;
  font-size: 1.2rem;
}
.resumeEditor h2 {
  font-size: 3rem;
  margin: .5em 0;
}
.resumeEditor hr {
  margin: .5em 0;
}
.resumeEditor ul,.resumeEditor ol{
  list-style: none;
  display: block;
  width: 27vw;
  height: 30vh;
  float: left;
  padding: .5em;
  font-size: 1.6rem;
}
.resumeEditor ul:last-child {
  height: 35vh;
}
.resumeEditor ul {
  border-right: 1px solid rgba(0,0,0,.6);
}
.resumeEditor ul ul {
  width: 100%;
  height: 100%; 
  font-size: 1.2rem;
  float: none;
  padding: 0;
  border: 0;
  margin-top: .5em;
}
.resumeEditor ul ul > li::before{
  content: '';
  margin-right: .5em;
}
.resumeEditor ul ul > li {
  padding: .2em;
} 
.resumeEditor ol ol {
  width: 100%;
  height: 100%;
  font-size: 1.2rem;
  margin-top: .5em;
  padding: 0;
  float: none;
}
.resumeEditor ol ol > li {
  padding: .2em;
} 
.resumeEditor ol ol > li::before {
  content: '';
  margin-right: .5em;
}
.resumeEditor ol > li > a {
  text-decoration: none;
  color: rgb(79,129,189);
}
`, `/* 
 */
.thankEditor {
  position: relative; 
  left: 10vw; 
  top: 0;
  background: rgba(64,191,241,.4);
  margin-top: 4rem;
}
.thankEditor {
  width: 80vw; 
  height: 25vh;
  border: 1px solid #ccc;
  color: #001C42;
  font-size: 1.6rem;
  overflow: hidden;
}
`,`
.thankEditor {
  padding: .5rem;
}
.thankEditor h2 {
  font-size: 1.6rem;
}
.thankEditor hr {
  margin: .5rem 0;
}
.thankEditor {
  -webkit-transition: none;
  transition: none;
  -webkit-transform: rotateX(10deg) translateZ(-100px) ;
  -ms-transform: rotateX(10deg) translateZ(-100px) ;
  -o-transform: rotateX(10deg) translateZ(-100px) ;
  transform: rotateX(10deg) translateZ(-100px) ;
}
`],
        currentMarkdown: '',
        currentThankMarkdown: '',
        fullMarkdown: `   
----


***

- 
    - 1995.8.26
    - 
    - 13617098915
    - JK-RE@outlook.com


1. 
    1. CET-4
    1. HTMLCSSJavascriptWeb
    1. DIV+CSSHTML5CSS3AJAXJquery
    1. ES6JSONVuewebpackBootstrapPS


- 
    - 2013.9-2017.6   
    -   C


2. 
    1. [](#)
    1. :[](http://crazyhey.cn/megumi.html)/[](http://crazyhey.cn)


- 
    - 
    - 
    - CAD
    - 
      


3. 
    1. 
       
       HTML+CSS+JS
       18


- 
    - 2017.8-2018.2  
    - 
    - 
    - 
    - 

`, thanksMarkdown: `
      
----

***

        ,
    
    !

  `
      }
    },
    created() {
      this.makeResume()
    },

    methods: {
      makeResume: async function () {
        await this.progressivelyShowStyle(0);
        await this.progressivelyShowResume();
        await this.progressivelyShowStyle(1);
        await this.showHtml();
        await this.progressivelyShowStyle(2);
        await this.progressivelyShowThanks();
        await this.progressivelyShowStyle(3)
      },
      showHtml() {
        return new Promise((resolve, reject) => {
          this.enableHtml = true
          resolve()
        })
      },
      progressivelyShowStyle(n) {
        return new Promise((resolve, reject) => {
          let interval = this.interval
          let showStyle = (async function () {
            let style = this.fullStyle[n]
            if (!style) {
              return
            }
            //  n  style 
            let length = this.fullStyle.filter((_, index) => index <= n).map((item) => item.length).reduce((p, c) => p + c, 0)
            let prefixLength = length - style.length
            if (this.currentStyle.length < length) {
              let l = this.currentStyle.length - prefixLength
              let char = style.substring(l, l + 1) || ' '
              this.currentStyle += char
              if (style.substring(l - 1, l) === '\n' && this.$refs.styleEditor) {
                this.$nextTick(() => {
                  this.$refs.styleEditor.goBottom()
                })
              }
              setTimeout(showStyle, interval)
            } else {
              resolve()
            }
          }).bind(this)
          showStyle()
        })
      },
      progressivelyShowResume() {
        return new Promise((resolve, reject) => {
          let length = this.fullMarkdown.length
          let interval = this.interval
          let showResume = () => {
            if (this.currentMarkdown.length < length) {
              this.currentMarkdown = this.fullMarkdown.substring(0, this.currentMarkdown.length + 1)
              let lastChar = this.currentMarkdown[this.currentMarkdown.length - 1]
              let prevChar = this.currentMarkdown[this.currentMarkdown.length - 2]
              if (prevChar === '\n' && this.$refs.resumeEditor) {
                this.$nextTick(() => this.$refs.resumeEditor.goBottom())
              }
              setTimeout(showResume, interval)
            } else {
              resolve()
            }
          }
          showResume()
        })
      },
      progressivelyShowThanks() {
        return new Promise((resolve, reject) => {
          let length = this.thanksMarkdown.length
          let interval = this.interval
          let showThanks = () => {
            if (this.currentThankMarkdown.length < length) {
              this.currentThankMarkdown = this.thanksMarkdown.substring(0, this.currentThankMarkdown.length + 1)
              let lastChar = this.currentThankMarkdown[this.currentThankMarkdown.length - 1]
              let prevChar = this.currentThankMarkdown[this.currentThankMarkdown.length - 2]
              if (prevChar === '\n' && this.$refs.thankEditor) {
                this.$nextTick(() => this.$refs.thankEditor.goBottom())
              }
              setTimeout(showThanks, interval)
            } else {
              resolve()
            }
          }
          showThanks()
        })
      }
    }
  }

</script>

<style scoped>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  html {
    min-height: 100vh;
  }

  * {
    box-sizing: border-box;
  }
</style>



